* {
  margin: 0;
}
.app {
  background-color: #fafafa;
}

.app__header {
  position: sticky;
  top: 0;
  z-index: 1;
  object-fit: contain;
  padding: 20px;
  background-color: white;
  border-bottom: 1px solid lightgray;
  display: flex;
  justify-content: space-between;
}
.app__headerImage {
  object-fit: contain;
}

.app__signUp {
  display: flex;
  width: 90%;
  flex-direction: column;
}
.app__posts {
  padding: 1.2rem;
  display: flex;
  justify-content: center;
}
.app__postsRight {
  margin-left: 20px;
}
