.post {
  max-width: 500px;
  background-color: white;
  border: 1px solid lightgray;
  margin-bottom: 45px;
}
.post__Image {
  width: 100%;
  object-fit: contain;
  border-top: 1px solid lightgray;
  border-bottom: 1px solid lightgray;
}

.post__text {
  font-weight: normal;
  padding: 20px;
}
.post__header {
  display: flex;
  align-items: center;
  padding: 20px;
}
.post__avatar {
  margin-right: 10px;
}
.post__commentBox {
  display: flex;
  margin-top: 10px;
}

.post__input {
  flex: 1;
  border: none;
  border-top: 1px solid lightgray;
  padding: 0.5rem;
}
.post__input:focus {
  outline: none;
}
.post__button {
  flex: 0;
  border: none;
  border-top: 1px solid lightgray;
  background-color: transparent;
  color: #6082a3;
}
.post__comments {
  padding: 20px;
}
